*{
  text-decoration: none;
  transition: all .4s ease-in-out; 
  list-style-type: none;
  font-family: sans-serif; 
  margin: 0;
  --red: #c91b1b;
  --white: #ffffff; 
  --text: #1F1F1F;
  --black: #1d1818;
  --blue: #287cdb;
  --darkblue: #103C4A;
  --textBlue: #1665be;
  --shadow: 1px 1px 8px #707070;
} 

/* HEADER.JS CSS */
.site-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  box-shadow: var(--shadow);
  background: var(--white);
}
.site-details{
  display: flex;
  gap: 2rem;
  align-items: center;
  justify-content: center;
}
.didi{
  display: none;
}
.site-name{
  color: var(--darkblue);
  font-size: 2rem;
}
.site-nav2, .site-details2{
  display: none;
}
.nav-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}
.nav-wrap a{
  color: var(--darkblue);
  font-size: 17px;
}
.nav-wrap .bordered{
  border-left: 1px solid var(--darkblue);
  padding: 0.3rem;
}
.bordered{
  position: relative;
}
.cart-count{
  padding: 4px 7px;
  background: var(--red);
  color: var(--white);
  border-radius: 50%;
  position: inherit;
  font-size: .5rem;
  font-weight: bold;
  position: absolute;
  top: -9px;
  right: -6px;
  border: 1px solid #fff;
}

/* HOME.JS CSS */
.body-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
  height: 98vh;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5)), url(/public/images/shopping2.jpg) top no-repeat;
  background-size: cover;
  margin: 0;
}
.body-title{
  font-size: 4rem;
  color: #fff;
}
.body-text{
  font-size: 1.2rem;
  color: #fff;
}
.start-shopping{
  background-color: var(--darkblue);
  color: var(--white);
  border: 0px;
  border-radius: 10px;
  padding: .6rem 1rem;
  font-size: .9rem;
  cursor: pointer;
  border: 2px solid var(--darkblue);
}
.start-shopping:hover{
  background: transparent;
  color: #fff;
  border: 2px solid #fff;
}
/* Side Nav */
.side-nav{
  display: none;
  position: fixed;
  top: 0%;
  background: var(--darkblue);
  width: 50%;
  left: -110%;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  align-items: flex-start;
  height: auto;
  padding: 1rem .3rem;
  overflow-x: hidden;
}
.side-nav a{
  color: #fff;
}
.close-sidenav{
  border: none;
  padding: 0.5rem .7rem;
  font-size: 2rem;
  background: none;
  color: #fff;
  cursor: pointer;
}
.nav-top{
  color: #fff;
  display: flex;
  gap: 1rem;
  align-items: center;
  width: 100%;
  padding: 0.5rem;
}
.nav-mid, .nav-bottom{
  display: flex;
  flex-direction: column;
  gap: .4rem;
  text-align: left;
  width: 100%;
}
.nav-mid{
  margin-bottom: 1rem;
}
.nav-mid a, .nav-bottom a{
  display: flex;
  gap: 1rem;
  align-items: center;
  padding: 0.6rem .6rem .7rem;
}
.nav-mid a:hover, .nav-bottom a:hover{
  background: #154f61;
}

.show-sidenav{
  left: 0%;
}


/* PRODUCTLIST.JS CSS */
.products-title{
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.list-title{
  font-family: Orelega One;
  font-size: 3rem;
  font-weight: bolder;
  line-height: 65.42px;
  letter-spacing: 0.005em;
  text-align: left;
  color: var(--darkblue);
}
.title-left{
  padding-left: 1rem;
}
.title-text{
  font-family: Orelega One;
  font-size: 20px;
  font-weight: bolder;
  line-height: 21.81px;
  letter-spacing: 0.005em;
  text-align: left;
  color: var(--darkblue);
}
.filter{
  padding: 0.4rem .8rem;
  font-size: 1rem;
  color: var(--darkblue);
  background: #ffffff;
  border-radius: .3rem;
  border: 2px solid var(--darkblue);
  cursor: pointer; 
  display: flex;
  gap: 1rem;
}
.filter:hover{
  background: var(--darkblue);
  color: #fff;
}

/* PRODUCTS LIST */
.products-cover{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 1.5rem;
  gap: 1rem;
}
.success-msg{
  display: none;
  padding: .8rem;
  position: fixed;
  background: var(--darkblue);
  color: #fff;
  width: 100%;
  top: 5%;
  border-radius: .5rem;
}
.removed{
  background: var(--red);
}
.order-confirm p a{
  color: var(--darkblue);
  padding: .3rem;
  border-radius: .3rem;
  background: #fff;
}
.product-like{
  color: var(--red);
  display: flex;
  justify-content: end;
  padding: 1rem 1rem 0rem;
  align-items: start;
  font-size: 1.2rem;
  cursor: pointer;
}
.product-like svg{
  width: 1rem;
}
.product-card{
  display: flex;
  flex-direction: column;
  background: #F6F6F6;
  width: 19rem;
  height: 377px;
  border-radius: .7rem;
  margin-bottom: 1.5rem;
}
.product-img{
  width: 18rem;
  height: 15rem;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.product-img img{
  border-radius: 8px;
  object-fit: cover;
  width: 13rem;
  padding-bottom: 10px;
}

.product-info{
  display: flex;
  align-items: start;
  flex-direction: column;
  gap: 0.5rem;
  padding-left: 1rem;
}
.product-name{
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 22.5px;
  color: var(--darkblue);
}
.product-name a{
  color: var(--darkblue);
}
.product-desc{
  color: #1F1F1FCC;
  font-size: .9rem;
  line-height: 19.5px;
}
.product-price{
  color: var(--text);
  font-size: 1.1rem;
  font-weight: 700;
}
.price-button{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: .4rem 1rem 0 1rem;
}
.addCart-btn{
  padding: .5rem .7rem;
  color: #103C4A;
  border-radius: .3rem;
  border: 2px solid #103C4A;
  cursor: pointer;
  font-size: 1rem;
}
.addCart-btn:hover{
  background: #103C4A;
  color: white;
}

.product-paging{
  display: flex;
  gap: .7rem;
  justify-content: center;
}
.btn1, .btn2, .btn3, .btn4, .btn5{
  padding: 1rem;
  border-radius: 5px;
  background: transparent;
  font-weight: 800;
  color: #103C4A;
  border: none;
}
.btn1, .btn2, .btn5{
  border: 2px solid #dae3e6;
}
.btn1, .btn3{
  color: #b6c1c5;
}
.btn2{
  background: #103C4A;
  color: #ffffff;
}

/* FOOTER.JS CSS */
.page-footer{
  background: #103C4A;
}
.widget-wrap{
  display: flex;
  justify-content: space-between;
  padding: 3rem 2rem 1rem 2rem;
  margin-top: 2rem;
  align-items: start;
  color: #ffffff;
  flex-wrap: wrap;
}
.widget {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  align-items: start;
}
.widget p a{
  color: #ffffff;
  font-size: 1rem;
}

.footer-site-name{
  font-size: 35px;
  text-align: left;
  padding-bottom: 2rem;
}
.footer-text{
  width: 20rem;
  text-align: left;
  padding-bottom: 1.rem;
}
.social-links{
  display: flex;
  gap: 1rem;
  justify-content: center;
  padding: 1.5rem;
  width: 10rem;
}
.social-links a{
  color: #ffffff;
  font-size: 1rem;
}
.sub-head{
  font-size: 1rem;
  font-weight: bold;
}
.left-widget{
  display: flex;
  justify-content: start;
  align-items: start;
  padding: 1rem;
}
.site-ref{
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  padding: 0 1rem 1rem;
}
.footer-bottom-text, .foot-right a{
  color: #fff;
}
.foot-right{
  text-align: right;
  display: flex;
  gap: 2rem;
}

/* CART.JS CSS */
.page-title, .num-name{
  color: var(--darkblue);
  font-weight: 500;
  font-size: 1.5rem;
  text-align: left;
  padding: 2rem 0 1rem 2rem;
}
.page-cover{
  padding: 1.5rem;
  font-family: open-sans;
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}
.cart-items-cover{
  width: 100%;
}
.cart-items{
  padding: .5rem;
  background: #F6F6F6;
  border-radius: .6rem;
  display: flex;
  margin-bottom: .5rem;
}
.item-cover{
  display: flex;
  padding: 1rem;
  align-items: start;
  flex-direction: column;
  width: 100%;
  gap: .5rem;
  flex-wrap: wrap;
}
.col1{
  width: 10rem;
  height: 10rem;
  /* background: red; */
}
.col1 img{
  width: 11rem;
  object-fit:fill;
  border-radius: .5rem;
}
.item-count{
  color: var(--darkblue);
  font-size: 1rem;
  font-weight: 700;
  padding: 0rem .2rem;
}
.slice-div{
  display: flex;
  color: var(--darkblue);
  width: 100%;
}
.product-details{
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  width: 100%;
}
.left{
  display: flex;
  flex-direction: column;
  gap: .7rem;
}
.left p{
  text-align: left;
  font-size: medium;
}
.left .p-name{
  font-size: larger;
  font-weight: 500;
}
.right{
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.right p{
  text-align: right;
}
.amount, .discount{
  font-weight: 600;
}
.m-amount{
  text-decoration: line-through;
}
.percent{
  background: green;
  padding: 0.3rem;
  border-radius: .2rem;
  color: #ffffff;
  font-size: small;
}

.link-share .link{
  background: var(--darkblue);
  color: #ffffff;
  padding: .5rem;
  border-radius: 50%;
}

.bottom-cut{
  width: 100%;
}
.bottom-cut .bottom{
  display: flex;
  justify-content: space-between;
  padding: .3rem 0 .5rem 1rem;
}
.remove-btn{
  padding: 0.5rem 1.3rem;
  gap: 1rem;
  display: flex;
  color: var(--red);
  border: 1px solid var(--red);
  border-radius: .3rem;
  font-weight: 600;
}
.remove-btn:hover{
  background: var(--red);
  color: #ffffff;
  cursor: pointer;
}
.bottom-right{
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
}
.bottom-right .increment, .bottom-right .decrement{
  background: var(--darkblue);
  border-radius: .3rem;
  color: #ffffff;
  padding: 0.2rem .5rem;
  border: none;
}

.items-checkout{
  background: #F6F6F6;
  width: 25%;
  padding: .7rem;
  border-radius: .6rem;
  height: 9rem;
  justify-content: start;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: start;
}
.check-title{
  text-align: left;
  color: var(--darkblue);
  font-weight: bold;
}
.sub-total{
  display: flex;
  justify-content: space-between;
  color: var(--darkblue);
}
.checkout-btn, .pay-btn{
  background: #FFAE1B;
  box-shadow: var(--shadow);
  padding: 0.5rem;
  border-radius: .5rem;
  border: none;
  cursor: pointer;
  color: var(--darkblue);
  font-weight: 600;
  font-size: 1rem;
}
.checkout-btn a{
  color: var(--darkblue);
  padding: 1rem;
}

/* CHECKOUT.JS CSS */
.checkout-body-wrap{
  width: 100%;
}
.head-covering .page-title{
  display: flex;
  gap: 2rem;
}
.head-covering .page-title button{
  background: #F6F6F6;
  padding: 0.2rem 1rem;
  border-radius: .3rem;
  color: var(--darkblue);
  border: none;
  cursor: pointer;
}
.head-covering .page-title button:hover{
  background: var(--darkblue);
  color: #fff;
}
.row-container{
  display: flex;
  justify-content: start;
  padding: 1rem;
  background: #F6F6F6;
  margin: 1rem;
}
.check-icon{
  color: green;
  font-size: larger;
}
.check-right{
  display: flex;
  justify-content: start;
  flex-direction: column;
  width: 100%;
}
.check-row1{
  display: flex;
  justify-content: space-between;
  align-items: start;
}
.check-row1, .check-row2, .check-row3, .customer-address{
  padding-left: 1rem;
  background: #F6F6F6;
  color: #1F1F1F;
}
.num-name{
  font-size: 1.2rem;
  padding: 0rem 0 1rem 0rem;
  color: #1F1F1F;
  font-weight: 600;
}
.change{
  font-weight: 600;
  color: #1F1F1F;
  display: flex;
  gap: 1rem;
  align-items: center;
}
.check-row2{
  display: flex;
  gap: 1rem;
  padding: 1rem;
  width: 100%;
}
.check-row2 p{
  text-align: left;
  font-weight: 600;
}
.customer-address{
  display: flex;
  justify-content: start;
  gap: 1.5rem;
}
input[type=radio]{
  padding: 0;
  width: 5%;
  height: 1.3rem;
}
.check-sub, .pay-del, .pay-with, .install{
  font-weight: 600;
  font-size: large;
  text-align: left;
}
.stand{
  font-weight: bold;
}
.radio-title{
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: start;
  text-align: left;
  gap: 1rem;
  width: 100%;
}
.payment-channel{
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  width: 100%;
  align-items: center;
}
.pay-img{
  width: 14rem;
}
hr{
  margin: 1rem 0;
}
.check4{
  flex-direction: column;
  align-items: flex-start;
}
.radio-btn-cover{
  display: flex;
  text-align: left;
  align-items: center;
  gap: 1rem;
}
.pay-btn-cover{
  display: flex;
  justify-content: flex-end;
}
.pay-btn{
  padding: 1rem 2rem;
}

/* NOTFOUND.JS CSS */
.not-found{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
  height: 70vh;
  color: var(--text);
}
.not-found div{
  display: flex;gap: 1rem;
}
.not-found div p a{
  background: #dae3e6;
  padding: .6rem 1rem;
  border-radius: .3rem;
  color: #103C4A;
}
.go-home a:hover{
  background: var(--darkblue);
  color: #fff;
}
.notfound-text{
  font-size: 3rem;
}
.go-home a{
  font-size: 1.3rem;
  color: var(--blue);
}


@media screen and (max-width: 600px) {
  * {
    transition: all 0.7s ease-in-out;
  }
  .site-nav,.site-details{
    display: none;
  }
  .site-nav2, .site-details2{
    display: block;
    cursor: pointer;
  }
  .didi{
    display: flex;
    justify-content: space-between;
    width: 60%;
    align-items: center;
  }
  .site-details2{
    display: flex;
    color: var(--darkblue);
    background: transparent;
    border: none;
    padding: .1rem .8rem;
  }
  h2 a{
    color: var(--darkblue);
  }
  .footer-text{
    display: none;
  }
  .left-widget{
    width: 100%;
    justify-content: center;
  }
  .footer-site-name{
    padding-bottom: 0;
  }
  .page-cover{
    flex-wrap: wrap;
  }
  .items-checkout{
    width: 100%;
  }
  .col1{
    display: flex;
    width: 100%;
    justify-content: center;
  }
  .cart-items, .slice-div{
    flex-wrap: wrap;
  }
  .checkout-btn{
    padding: 1.5rem;
  }
  .items-checkout{
    height: auto;
  }
  .customer-address{
    flex-direction: column;
    justify-content: start;
    text-align: left;
    gap: 0.4rem;
  }
  .pay-btn-cover{
    justify-content: center;
  }
  .body-wrap{
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5)), url(/public/images/shopping2.jpg) top no-repeat;
  }
  /* .cart-items{
    transform: scale(0.8);
  } */
}